// NGRX
import { Action } from "@ngrx/store";
import { Update } from "@ngrx/entity";
// CRUD
import { NegotiationCodeModel } from "./negotiationCode.model";
import { QueryNegotiationCodeModel } from "./querynegotiationCode.model";
// Models

export enum NegotiationCodeActionTypes {
	AllUsersRequested = "[NegotiationCode Module] All NegotiationCode Requested",
	AllUsersLoaded = "[NegotiationCode API] All NegotiationCode Loaded",
	NegotiationCodeOnServerCreated = "[Edit NegotiationCode Component] NegotiationCode On Server Created",
	NegotiationCodeCreated = "[Edit NegotiationCode Dialog] NegotiationCode Created",
	NegotiationCodeUpdated = "[Edit NegotiationCode Dialog] NegotiationCode Updated",
	NegotiationCodeDeleted = "[NegotiationCode List Page] NegotiationCode Deleted",
	NegotiationCodePageRequested = "[NegotiationCode List Page] NegotiationCode Page Requested",
	NegotiationCodePageLoaded = "[NegotiationCode API] NegotiationCode Page Loaded",
	NegotiationCodePageCancelled = "[NegotiationCode API] NegotiationCode Page Cancelled",
	NegotiationCodePageToggleLoading = "[NegotiationCode] NegotiationCode Page Toggle Loading",
	NegotiationCodeActionToggleLoading = "[NegotiationCode] NegotiationCode Action Toggle Loading",
}
export class NegotiationCodeOnServerCreated implements Action {
	readonly type = NegotiationCodeActionTypes.NegotiationCodeOnServerCreated;
	constructor(public payload: { negotiationCode: NegotiationCodeModel }) {}
}

export class NegotiationCodeCreated implements Action {
	readonly type = NegotiationCodeActionTypes.NegotiationCodeCreated;
	constructor(public payload: { negotiationCode: NegotiationCodeModel }) {}
}

export class NegotiationCodeUpdated implements Action {
	readonly type = NegotiationCodeActionTypes.NegotiationCodeUpdated;
	constructor(
		public payload: {
			partialNegotiationCode: Update<NegotiationCodeModel>;
			negotiationCode: NegotiationCodeModel;
		}
	) {}
}

export class NegotiationCodeDeleted implements Action {
	readonly type = NegotiationCodeActionTypes.NegotiationCodeDeleted;

	constructor(public payload: { id: string }) {}
}

export class NegotiationCodePageRequested implements Action {
	readonly type = NegotiationCodeActionTypes.NegotiationCodePageRequested;
	constructor(public payload: { page: QueryNegotiationCodeModel }) {}
}

export class NegotiationCodePageLoaded implements Action {
	readonly type = NegotiationCodeActionTypes.NegotiationCodePageLoaded;
	constructor(public payload: { negotiationCode: NegotiationCodeModel[]; totalCount: number; page: QueryNegotiationCodeModel }) {}
}

export class NegotiationCodePageCancelled implements Action {
	readonly type = NegotiationCodeActionTypes.NegotiationCodePageCancelled;
}

export class NegotiationCodePageToggleLoading implements Action {
	readonly type = NegotiationCodeActionTypes.NegotiationCodePageToggleLoading;
	constructor(public payload: { isLoading: boolean }) {}
}

export class NegotiationCodeActionToggleLoading implements Action {
	readonly type = NegotiationCodeActionTypes.NegotiationCodeActionToggleLoading;
	constructor(public payload: { isLoading: boolean }) {}
}

export type NegotiationCodeActions = NegotiationCodeCreated | NegotiationCodeUpdated | NegotiationCodeDeleted | NegotiationCodeOnServerCreated | NegotiationCodePageLoaded | NegotiationCodePageCancelled | NegotiationCodePageToggleLoading | NegotiationCodePageRequested | NegotiationCodeActionToggleLoading;
