// NGRX
import { createFeatureSelector, createSelector } from "@ngrx/store";
// CRUD
import { QueryResultsModel, HttpExtenstionsModel } from "../_base/crud";
// State
import { NegotiationCodeState } from "./negotiationCode.reducer";
import { each } from "lodash";
import { NegotiationCodeModel } from "./negotiationCode.model";

export const selectNegotiationCodeState = createFeatureSelector<NegotiationCodeState>("negotiationCode");

export const selectNegotiationCodeById = (negotiationCodeId: string) => createSelector(selectNegotiationCodeState, (negotiationCodeState) => negotiationCodeState.entities[negotiationCodeId]);

export const selectNegotiationCodePageLoading = createSelector(selectNegotiationCodeState, (negotiationCodeState) => {
	return negotiationCodeState.listLoading;
});

export const selectNegotiationCodeActionLoading = createSelector(selectNegotiationCodeState, (negotiationCodeState) => negotiationCodeState.actionsloading);

export const selectLastCreatedNegotiationCodeId = createSelector(selectNegotiationCodeState, (negotiationCodeState) => negotiationCodeState.lastCreatedNegotiationCodeId);

export const selectNegotiationCodePageLastQuery = createSelector(selectNegotiationCodeState, (negotiationCodeState) => negotiationCodeState.lastQuery);

export const selectNegotiationCodeInStore = createSelector(selectNegotiationCodeState, (negotiationCodeState) => {
	const items: NegotiationCodeModel[] = [];
	each(negotiationCodeState.entities, (element) => {
		items.push(element);
	});
	const httpExtension = new HttpExtenstionsModel();
	const result: NegotiationCodeModel[] = httpExtension.sortArray(items, negotiationCodeState.lastQuery.sortField, negotiationCodeState.lastQuery.sortOrder);
	return new QueryResultsModel(result, negotiationCodeState.totalCount, "");
});

export const selectNegotiationCodeShowInitWaitingMessage = createSelector(selectNegotiationCodeState, (negotiationCodeState) => negotiationCodeState.showInitWaitingMessage);

export const selectHasNegotiationCodeInStore = createSelector(selectNegotiationCodeState, (queryResult) => {
	if (!queryResult.totalCount) {
		return false;
	}

	return true;
});
