// NGRX
import { createFeatureSelector } from "@ngrx/store";
import { EntityState, EntityAdapter, createEntityAdapter } from "@ngrx/entity";
// Actions
import { NegotiationCodeActions, NegotiationCodeActionTypes } from "./negotiationCode.action";
// CRUD

// Models
import { NegotiationCodeModel } from "./negotiationCode.model";
import { QueryNegotiationCodeModel } from "./querynegotiationCode.model";

// tslint:disable-next-line:no-empty-interface
export interface NegotiationCodeState extends EntityState<NegotiationCodeModel> {
	listLoading: boolean;
	actionsloading: boolean;
	totalCount: number;
	lastCreatedNegotiationCodeId: string;
	lastQuery: QueryNegotiationCodeModel;
	showInitWaitingMessage: boolean;
}

export const adapter: EntityAdapter<NegotiationCodeModel> = createEntityAdapter<NegotiationCodeModel>({ selectId: (model) => model._id });

export const initialNegotiationCodeState: NegotiationCodeState = adapter.getInitialState({
	listLoading: false,
	actionsloading: false,
	totalCount: 0,
	lastQuery: new QueryNegotiationCodeModel({}),
	lastCreatedNegotiationCodeId: undefined,
	showInitWaitingMessage: true,
});

export function negotiationCodeReducer(state = initialNegotiationCodeState, action: NegotiationCodeActions): NegotiationCodeState {
	switch (action.type) {
		case NegotiationCodeActionTypes.NegotiationCodePageToggleLoading:
			return {
				...state,
				listLoading: action.payload.isLoading,
				lastCreatedNegotiationCodeId: undefined,
			};
		case NegotiationCodeActionTypes.NegotiationCodeActionToggleLoading:
			return {
				...state,
				actionsloading: action.payload.isLoading,
			};
		case NegotiationCodeActionTypes.NegotiationCodeOnServerCreated:
			return {
				...state,
			};
		case NegotiationCodeActionTypes.NegotiationCodeCreated:
			return adapter.addOne(action.payload.negotiationCode, {
				...state,
				lastCreatedBlockId: action.payload.negotiationCode._id,
			});
		case NegotiationCodeActionTypes.NegotiationCodeUpdated:
			return adapter.updateOne(action.payload.partialNegotiationCode, state);
		case NegotiationCodeActionTypes.NegotiationCodeDeleted:
			return adapter.removeOne(action.payload.id, state);
		case NegotiationCodeActionTypes.NegotiationCodePageCancelled:
			return {
				...state,
				listLoading: false,
				lastQuery: new QueryNegotiationCodeModel({}),
			};
		case NegotiationCodeActionTypes.NegotiationCodePageLoaded: {
			return adapter.addMany(action.payload.negotiationCode, {
				...initialNegotiationCodeState,
				totalCount: action.payload.totalCount,
				lastQuery: action.payload.page,
				listLoading: false,
				showInitWaitingMessage: false,
			});
		}
		default:
			return state;
	}
}

export const getNegotiationCodeState = createFeatureSelector<NegotiationCodeState>("negotiationCode");

export const { selectAll, selectEntities, selectIds, selectTotal } = adapter.getSelectors();
