import { BaseModel } from "../_base/crud";

export class NegotiationCodeModel extends BaseModel {
	_id: string;
	name: string;
	type: string;
	time: string;
	price: any;
	price_float: any;

	createdDate: string;
	updatedDate: string;
	updatedBy: string;

	clear(): void {
		this._id = undefined;
		this.name = "";
		this.type = "";
		this.time = "";
		this.price = undefined;
		this.price_float = undefined;

		this.createdDate = "";
		this.updatedBy = "";
		this.updatedDate = "";
	}
}
