// RxJS
import { of } from "rxjs";
import { catchError, finalize, tap, debounceTime, delay, distinctUntilChanged } from "rxjs/operators";
// NGRX
import { Store, select } from "@ngrx/store";
// CRUD
import { BaseDataSource, QueryResultsModel } from "../_base/crud";
// State
import { AppState } from "../../core/reducers";
import { selectNegotiationCodeInStore, selectNegotiationCodePageLoading, selectNegotiationCodeShowInitWaitingMessage } from "./negotiationCode.selector";

export class NegotiationCodeDataSource extends BaseDataSource {
	constructor(private store: Store<AppState>) {
		super();

		this.loading$ = this.store.pipe(select(selectNegotiationCodePageLoading));

		this.isPreloadTextViewed$ = this.store.pipe(select(selectNegotiationCodeShowInitWaitingMessage));

		this.store.pipe(select(selectNegotiationCodeInStore)).subscribe((response: QueryResultsModel) => {
			this.paginatorTotalSubject.next(response.totalCount);
			this.entitySubject.next(response.items);
		});
	}
}
