// Angular
import { Injectable } from "@angular/core";
// RxJS
import { mergeMap, map, tap } from "rxjs/operators";
import { Observable, defer, of, forkJoin } from "rxjs";
// NGRX
import { Effect, Actions, ofType } from "@ngrx/effects";
import { Store, select, Action } from "@ngrx/store";
// CRUD
import { QueryResultsModel } from "../_base/crud";
// Services
import { NegotiationCodeService } from "./negotiationCode.service";
// State
import { AppState } from "../../core/reducers";
import { NegotiationCodeActionTypes, NegotiationCodePageRequested, NegotiationCodePageLoaded, NegotiationCodeCreated, NegotiationCodeDeleted, NegotiationCodeUpdated, NegotiationCodeOnServerCreated, NegotiationCodeActionToggleLoading, NegotiationCodePageToggleLoading } from "./negotiationCode.action";
import { QueryNegotiationCodeModel } from "./querynegotiationCode.model";

@Injectable()
export class NegotiationCodeEffect {
	showPageLoadingDistpatcher = new NegotiationCodePageToggleLoading({ isLoading: true });
	hidePageLoadingDistpatcher = new NegotiationCodePageToggleLoading({ isLoading: false });

	showActionLoadingDistpatcher = new NegotiationCodeActionToggleLoading({ isLoading: true });
	hideActionLoadingDistpatcher = new NegotiationCodeActionToggleLoading({ isLoading: false });

	@Effect()
	loadNegotiationCodePage$ = this.actions$.pipe(
		ofType<NegotiationCodePageRequested>(NegotiationCodeActionTypes.NegotiationCodePageRequested),
		mergeMap(({ payload }) => {
			this.store.dispatch(this.showPageLoadingDistpatcher);
			const requestToServer = this.negotiationCode.getListNegotiationCode(payload.page);
			const lastQuery = of(payload.page);
			return forkJoin(requestToServer, lastQuery);
		}),
		map((response: any) => {
			console.log(JSON.stringify(response, null, 1), "response");
			let res: { errorMessage: string; totalCount: any; items: any };
			const result: QueryResultsModel = { items: response[0].data.data, totalCount: response[0].totalCount, errorMessage: "", data: [] };
			const lastQuery: QueryNegotiationCodeModel = response[1];
			return new NegotiationCodePageLoaded({
				negotiationCode: result.items,
				totalCount: result.totalCount,
				page: lastQuery,
			});
		})
	);

	@Effect()
	deleteNegotiationCode$ = this.actions$.pipe(
		ofType<NegotiationCodeDeleted>(NegotiationCodeActionTypes.NegotiationCodeDeleted),
		mergeMap(({ payload }) => {
			this.store.dispatch(this.showActionLoadingDistpatcher);
			return this.negotiationCode.deleteNegotiationCode(payload.id);
		}),
		map(() => {
			return this.hideActionLoadingDistpatcher;
		})
	);

	@Effect()
	updateNegotiationCode = this.actions$.pipe(
		ofType<NegotiationCodeUpdated>(NegotiationCodeActionTypes.NegotiationCodeUpdated),
		mergeMap(({ payload }) => {
			this.store.dispatch(this.showActionLoadingDistpatcher);
			return this.negotiationCode.updateNegotiationCode(payload.negotiationCode);
		}),
		map(() => {
			return this.hideActionLoadingDistpatcher;
		})
	);

	@Effect()
	createBlock$ = this.actions$.pipe(
		ofType<NegotiationCodeOnServerCreated>(NegotiationCodeActionTypes.NegotiationCodeOnServerCreated),
		mergeMap(({ payload }) => {
			this.store.dispatch(this.showActionLoadingDistpatcher);
			return this.negotiationCode.createNegotiationCode(payload.negotiationCode).pipe(
				tap((res) => {
					this.store.dispatch(new NegotiationCodeCreated({ negotiationCode: res }));
				})
			);
		}),
		map(() => {
			return this.hideActionLoadingDistpatcher;
		})
	);

	constructor(private actions$: Actions, private negotiationCode: NegotiationCodeService, private store: Store<AppState>) {}
}
