// Angular
import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";
import { RouterModule, Routes } from "@angular/router";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { HttpClientModule, HTTP_INTERCEPTORS } from "@angular/common/http";
// NGRX
import { StoreModule } from "@ngrx/store";
import { EffectsModule } from "@ngrx/effects";
// Translate
import { TranslateModule } from "@ngx-translate/core";
import { PartialsModule } from "../../partials/partials.module";
// Services
import { HttpUtilsService, TypesUtilsService, InterceptService, LayoutUtilsService } from "../../../core/_base/crud";
// Shared
import { ActionNotificationComponent } from "../../partials/content/crud";
// Components
import { NegotiationCodeComponent } from "./negotiationCode.component";
import { ListUnittypeComponent } from "./list-negotiationCode/list-negotiationCode.component";
import { AddNegotiationCodeComponent } from "./add-negotiationCode/add-negotiationCode.component";
import { ViewNegotiationCodeComponent } from "./view-negotiationCode/view-negotiationCode.component";

// Material
import { MatInputModule, MatPaginatorModule, MatProgressSpinnerModule, MatSortModule, MatTableModule, MatSelectModule, MatMenuModule, MatProgressBarModule, MatButtonModule, MatCheckboxModule, MatDialogModule, MatTabsModule, MatNativeDateModule, MatCardModule, MatRadioModule, MatIconModule, MatDatepickerModule, MatExpansionModule, MatAutocompleteModule, MAT_DIALOG_DEFAULT_OPTIONS, MatSnackBarModule, MatTooltipModule } from "@angular/material";

import { negotiationCodeReducer } from "../../../core/negotiationCode/negotiationCode.reducer";
import { NegotiationCodeEffect } from "../../../core/negotiationCode/negotiationCode.effect";

const routes: Routes = [
	{
		path: "",
		component: NegotiationCodeComponent,
		children: [
			{
				path: "",
				component: ListUnittypeComponent,
			},
			{
				path: "add",
				component: AddNegotiationCodeComponent,
			},
			{
				path: "edit/:id",
				component: AddNegotiationCodeComponent,
			},
			{
				path: "view/:id",
				component: ViewNegotiationCodeComponent,
			},
		],
	},
];

@NgModule({
	imports: [MatExpansionModule, CommonModule, HttpClientModule, PartialsModule, RouterModule.forChild(routes), StoreModule.forFeature("negotiationCode", negotiationCodeReducer), EffectsModule.forFeature([NegotiationCodeEffect]), FormsModule, ReactiveFormsModule, TranslateModule.forChild(), MatButtonModule, MatMenuModule, MatSelectModule, MatInputModule, MatTableModule, MatAutocompleteModule, MatRadioModule, MatIconModule, MatNativeDateModule, MatProgressBarModule, MatDatepickerModule, MatCardModule, MatPaginatorModule, MatSortModule, MatCheckboxModule, MatProgressSpinnerModule, MatSnackBarModule, MatTabsModule, MatTooltipModule, MatDialogModule],
	providers: [
		InterceptService,
		{
			provide: HTTP_INTERCEPTORS,
			useClass: InterceptService,
			multi: true,
		},
		{
			provide: MAT_DIALOG_DEFAULT_OPTIONS,
			useValue: {
				hasBackdrop: true,
				panelClass: "kt-mat-dialog-container__wrapper",
				height: "auto",
				width: "900px",
			},
		},
		HttpUtilsService,
		TypesUtilsService,
		LayoutUtilsService,
	],
	entryComponents: [ActionNotificationComponent, NegotiationCodeComponent],
	declarations: [NegotiationCodeComponent, ListUnittypeComponent, AddNegotiationCodeComponent, ViewNegotiationCodeComponent],
})
export class NegotiationCodeModule {}
