import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable, of } from "rxjs";
import { NegotiationCodeModel } from "./negotiationCode.model";
import { catchError, map } from "rxjs/operators";
import { environment } from "../../../environments/environment";
import { QueryResultsModel } from "../_base/crud";

import * as FileSaver from "file-saver";
import { QueryNegotiationCodeModel } from "./querynegotiationCode.model";
import axios from "axios";

const API_NEGOTIATIONCODE_URL = `${environment.baseAPI}/api/negotiation-codes`;
const API_CSV = `${environment.baseAPI}/api/excel/negotiationCode/export`;

@Injectable({
	providedIn: "root",
})
export class NegotiationCodeService {
	constructor(private http: HttpClient) {}
	// get list negotiation code
	getListNegotiationCode(queryParams: any) {
		return axios
			.get(`${API_NEGOTIATIONCODE_URL}?pagination[pageSize]=10&pagination[page]=1`)
			.then((res: any) => {
				const response = {
					status: "success",
					data: res.data,
					totalCount: 0,
				};

				return response;
			})
			.catch((error) => {
				console.log(error);
			});
	}

	findNegotiationCodeById(_id: string): Observable<any> {
		return this.http.get<any>(`${API_NEGOTIATIONCODE_URL}/${_id}`);
	}
	deleteNegotiationCode(negotiationCodeId: string) {
		const url = `${API_NEGOTIATIONCODE_URL}/delete/${negotiationCodeId}`;
		return this.http.delete(url);
	}
	updateNegotiationCode(negotiationCode: NegotiationCodeModel) {
		const url = `${API_NEGOTIATIONCODE_URL}/edit/${negotiationCode._id}`;
		return this.http.patch(url, negotiationCode);
	}
	createNegotiationCode(negotiationCode: NegotiationCodeModel): Observable<NegotiationCodeModel> {
		const httpHeaders = new HttpHeaders();
		httpHeaders.set("Content-Type", "application/json");
		return this.http.post<NegotiationCodeModel>(`${API_NEGOTIATIONCODE_URL}/create`, negotiationCode, { headers: httpHeaders });
	}

	private handleError<T>(operation = "operation", result?: any) {
		return (error: any): Observable<any> => {
			// TODO: send the error to remote logging infrastructure
			console.error(error); // log to console instead
			// Let the app keep running by returning an empty result.
			return of(result);
		};
	}

	generateNegotiationCodeCode(): Observable<QueryResultsModel> {
		const url = `${API_NEGOTIATIONCODE_URL}/generate/code`;
		return this.http.get<QueryResultsModel>(url);
	}

	exportExcel() {
		return FileSaver.saveAs(`${API_CSV}`, "export-negotiationCode.xlsx");
	}
}
