// RxJS
import { BehaviorSubject, Observable, of, Subscription } from "rxjs";
// NGRX
import { Store, select } from "@ngrx/store";
import { Update } from "@ngrx/entity";
import { AppState } from "../../../../core/reducers";
// Layout
import { SubheaderService, LayoutConfigService } from "../../../../core/_base/layout";
import { LayoutUtilsService, MessageType } from "../../../../core/_base/crud";
import { NegotiationCodeModel } from "../../../../core/negotiationCode/negotiationCode.model";
import { selectLastCreatedNegotiationCodeId, selectNegotiationCodeActionLoading, selectNegotiationCodeById } from "../../../../core/negotiationCode/negotiationCode.selector";
import { NegotiationCodeService } from "../../../../core/negotiationCode/negotiationCode.service";

@Component({
	selector: "kt-add-negotiationCode",
	templateUrl: "./add-negotiationCode.component.html",
	styleUrls: ["./add-negotiationCode.component.scss"],
})
export class AddNegotiationCodeComponent implements OnInit, OnDestroy {
	// Public properties
	codenum: any;
	negotiationCode: NegotiationCodeModel;
	NegotiationCodeId$: Observable<string>;
	oldNegotiationCode: NegotiationCodeModel;
	selectedTab = 0;
	loading$: Observable<boolean>;
	negotiationCodeForm: FormGroup;
	date1 = new FormControl(new Date());
	hasFormErrors = false;
	checker: boolean;
	loading: boolean;

	negotiationCodeName: string = "";
	faqs = [];
	features = [];
	selectedFeature: any[] = [];
	// Private properties
	private subscriptions: Subscription[] = [];
	constructor(private activatedRoute: ActivatedRoute, private router: Router, private negotiationCodeFB: FormBuilder, private subheaderService: SubheaderService, private layoutUtilsService: LayoutUtilsService, private store: Store<AppState>, private service: NegotiationCodeService, private layoutConfigService: LayoutConfigService) {}

	ngOnInit() {
		this.loading$ = this.store.pipe(select(selectNegotiationCodeActionLoading));
		const routeSubscription = this.activatedRoute.params.subscribe((params) => {
			const id = params.id;
			if (id) {
				this.store.pipe(select(selectNegotiationCodeById(id))).subscribe((res) => {
					if (res) {
						this.negotiationCode = res;
						this.oldNegotiationCode = Object.assign({}, this.negotiationCode);
					}
				});
			} else {
				this.negotiationCode = new NegotiationCodeModel();
				this.negotiationCode.clear();
			}

			this.createForm();
		});
		this.subscriptions.push(routeSubscription);
	}

	createForm() {
		if (this.negotiationCode._id) {
			this.negotiationCodeForm = this.negotiationCodeFB.group({
				name: [this.negotiationCode.name, Validators.required],
				type: [this.negotiationCode.type, Validators.required],
				time: [this.negotiationCode.time, Validators.required],
				price: [this.negotiationCode.price, Validators.required],
				price_float: [this.negotiationCode.price_float],
			});
		} else {
			// this.getNumber();
			this.negotiationCodeForm = this.negotiationCodeFB.group({
				name: ["", Validators.required],
				type: ["", Validators.required],
				time: ["", Validators.required],
				price: ["", Validators.required],
				price_float: ["", Validators.required],
			});
		}
	}

	goBackWithId() {
		const url = `/negotiationCode`;
		this.router.navigateByUrl(url, { relativeTo: this.activatedRoute });
	}

	refreshNegotiationCode(isNew: boolean = false, id: string = "") {
		let url = this.router.url;
		if (!isNew) {
			this.router.navigate([url], { relativeTo: this.activatedRoute });
			return;
		}

		url = `/negotiationCode/edit/${id}`;
		this.router.navigateByUrl(url, { relativeTo: this.activatedRoute });
	}

	onSubmit(withBack: boolean = false) {
		this.hasFormErrors = false;
		const controls = this.negotiationCodeForm.controls;
		/** check form */
		if (this.negotiationCodeForm.invalid) {
			Object.keys(controls).forEach((controlName) => controls[controlName].markAsTouched());

			this.hasFormErrors = true;
			this.selectedTab = 0;
			return;
		}

		this.loading = true;
		const editedNegotiationCode = this.prepareNegotiationCode();

		if (this.faqs.length === 0) {
			const message = `Menu not detected, please refresh page`;
			this.layoutUtilsService.showActionNotification(message, MessageType.Create, 5000, true, true);

			return;
		}

		if (editedNegotiationCode._id) {
			this.updateNegotiationCode(editedNegotiationCode, withBack);
			return;
		}

		this.addNegotiationCode(editedNegotiationCode, withBack);
	}
	prepareNegotiationCode(): NegotiationCodeModel {
		const controls = this.negotiationCodeForm.controls;
		const _negotiationCode = new NegotiationCodeModel();
		_negotiationCode.clear();
		_negotiationCode._id = this.negotiationCode._id;
		_negotiationCode.name = controls.name.value;
		_negotiationCode.type = controls.type.value;
		_negotiationCode.time = controls.time.value;
		_negotiationCode.price = controls.price.value;
		return _negotiationCode;
	}

	addNegotiationCode(_negotiationCode: NegotiationCodeModel, withBack: boolean = false) {
		const addSubscription = this.service.createNegotiationCode(_negotiationCode).subscribe(
			(res) => {
				const message = `New NegotiationCode successfully has been added.`;
				this.layoutUtilsService.showActionNotification(message, MessageType.Create, 5000, true, true);
				const url = `/negotiationCode`;
				this.router.navigateByUrl(url, { relativeTo: this.activatedRoute });
			},
			(err) => {
				console.error(err);
				const message = "Error while adding negotiationCode | " + err.statusText;
				this.layoutUtilsService.showActionNotification(message, MessageType.Create, 5000, true, false);
			}
		);
		this.subscriptions.push(addSubscription);
	}

	updateNegotiationCode(_negotiationCode: NegotiationCodeModel, withBack: boolean = false) {
		// Update User
		// tslint:disable-next-line:prefer-const
		const addSubscription = this.service.updateNegotiationCode(_negotiationCode).subscribe(
			(res) => {
				const message = `NegotiationCode successfully has been saved.`;
				this.layoutUtilsService.showActionNotification(message, MessageType.Update, 5000, true, true);
				const url = `/negotiationCode`;
				this.router.navigateByUrl(url, { relativeTo: this.activatedRoute });
			},
			(err) => {
				console.error(err);
				const message = "Error while adding negotiationCode | " + err.statusText;
				this.layoutUtilsService.showActionNotification(message, MessageType.Update, 5000, true, false);
			}
		);
		this.subscriptions.push(addSubscription);
	}

	getComponentTitle() {
		let result = "Buat Kode Negosiasi";
		if (!this.negotiationCode || !this.negotiationCode._id) {
			return result;
		}

		result = `Edit NegotiationCode`;
		return result;
	}

	onAlertClose($event) {
		this.hasFormErrors = false;
	}

	ngOnDestroy() {
		this.subscriptions.forEach((sb) => sb.unsubscribe());
	}
	/**
	 * toggleSubMenu
	 * @param item
	 */
	toggleSubMenu(item: any) {
		item.showSubMenu = !item.showSubMenu;
	}

	/**
	 * toggleNestedSubMenu
	 * @param parentSubMenu
	 * @param index
	 */
	toggleNestedSubMenu(parentSubMenu: any, index: number) {
		parentSubMenu.submenu[index].showSubMenu = !parentSubMenu.submenu[index].showSubMenu;
	}

	/**
	 * toggleNestedSubMenu
	 * @param parentSubMenu
	 * @param index
	 */
	toggleNestedSubMenu2(parentSubMenu: any, index: number, indexNested: number) {
		parentSubMenu.submenu[index].submenu[indexNested].showSubMenu = !parentSubMenu.submenu[index].submenu[indexNested].showSubMenu;
	}

	/**
	 * handleNegotiationCodeNameChange
	 * @param event
	 */
	handleNegotiationCodeNameChange(event: any): void {
		this.negotiationCodeName = event.target.value;
	}

	/**
	 * shouldCheckboxBeChecked
	 * @param faq
	 * @returns
	 */
	shouldCheckboxBeChecked(faq: any): boolean {
		if (this.negotiationCode._id) {
			// return faq.negotiationCode.includes(this.negotiationCode.negotiationCode);
		} else return false;
	}

	/**
	 * shouldFeatureCheckboxBeChecked
	 * @param faq
	 * @returns
	 */
	shouldFeatureCheckboxBeChecked(featureName: string, type: string): boolean {
		const controls = this.negotiationCodeForm.controls;
		const negotiationCodeName = controls.negotiationCode.value;

		const findSelectedFeature = this.features.find((data) => data.name === featureName);
		const findSelectedFeatureData = this.selectedFeature.find((data) => data.name === featureName);

		if (findSelectedFeatureData) {
			const findNegotiationCodeAccess = findSelectedFeatureData.negotiationCode_access.find((data) => data.negotiationCode === negotiationCodeName);

			if (findNegotiationCodeAccess) {
				if (type === "add") return findNegotiationCodeAccess.add;
				else if (type === "edit") return findNegotiationCodeAccess.edit;
				else if (type === "view") return findNegotiationCodeAccess.view;
				else if (type === "delete") return findNegotiationCodeAccess.delete;
				else if (type === "full-access") return findNegotiationCodeAccess.fullAccess;
				else return false;
			} else return false;
		} else if (findSelectedFeature) {
			const findNegotiationCodeAccess = findSelectedFeature.negotiationCode_access.find((data) => data.negotiationCode === negotiationCodeName);

			if (findNegotiationCodeAccess) {
				if (type === "add") return findNegotiationCodeAccess.add;
				else if (type === "edit") return findNegotiationCodeAccess.edit;
				else if (type === "view") return findNegotiationCodeAccess.view;
				else if (type === "delete") return findNegotiationCodeAccess.delete;
				else if (type === "full-access") return findNegotiationCodeAccess.fullAccess;
				else return false;
			} else return false;
		} else return false;
	}

	/**
	 * handleCheckboxDepth1
	 * @param event
	 * @param faqIndex
	 */
	handleCheckboxDepth1(event: any, faqIndex: number): void {
		const controls = this.negotiationCodeForm.controls;
		if (event.checked) {
			this.addNegotiationCodeToHierarchy(this.faqs[faqIndex], controls.negotiationCode.value);
		} else {
			this.removeNegotiationCodeFromHierarchy(this.faqs[faqIndex], controls.negotiationCode.value);
		}
	}

	/**
	 * handleCheckboxDepth2
	 * @param event
	 * @param faqIndex
	 * @param submenuIndex
	 */
	handleCheckboxDepth2(event: any, faqIndex: number, submenuIndex: number): void {
		const controls = this.negotiationCodeForm.controls;
		if (event.checked) {
			this.addNegotiationCodeToHierarchy(this.faqs[faqIndex].submenu[submenuIndex], controls.negotiationCode.value);
		} else {
			this.removeNegotiationCodeFromHierarchy(this.faqs[faqIndex].submenu[submenuIndex], controls.negotiationCode.value);
		}
	}

	/**
	 * handleCheckboxDepth3
	 * @param event
	 * @param faqIndex
	 * @param submenuIndex
	 * @param nestedIndex
	 */
	handleCheckboxDepth3(event: any, faqIndex: number, submenuIndex: number, nestedIndex: number): void {
		const controls = this.negotiationCodeForm.controls;
		if (event.checked) {
			this.addNegotiationCodeToHierarchy(this.faqs[faqIndex].submenu[submenuIndex].submenu[nestedIndex], controls.negotiationCode.value);
		} else {
			this.removeNegotiationCodeFromHierarchy(this.faqs[faqIndex].submenu[submenuIndex].submenu[nestedIndex], controls.negotiationCode.value);
		}
	}

	/**
	 * handleCheckboxDepth4
	 * @param event
	 * @param faqIndex
	 * @param submenuIndex
	 * @param nestedIndex
	 */
	handleCheckboxDepth4(event: any, faqIndex: number, submenuIndex: number, nestedIndex: number, nestedIndexAgain: number): void {
		const controls = this.negotiationCodeForm.controls;
		if (event.checked) {
			this.addNegotiationCodeToHierarchy(this.faqs[faqIndex].submenu[submenuIndex].submenu[nestedIndex].submenu[nestedIndexAgain], controls.negotiationCode.value);
		} else {
			this.removeNegotiationCodeFromHierarchy(this.faqs[faqIndex].submenu[submenuIndex].submenu[nestedIndex].submenu[nestedIndexAgain], controls.negotiationCode.value);
		}
	}

	/**
	 * handleCheckboxFeatureAccess
	 * @param event
	 * @param feature
	 * @param accessType
	 */
	handleCheckboxFeatureAccess(event: any, feature: string, accessType: string): void {
		// Find the index of the feature in selectedFeature array
		const controls = this.negotiationCodeForm.controls;
		const index = this.selectedFeature.findIndex((item) => item.name === feature);

		if (event.checked) {
			// If checked, add or update the feature in selectedFeature array
			if (index !== -1) {
				// Update the specific access type
				if (accessType === "full-access") {
					this.selectedFeature[index].negotiationCode_access[0]["add"] = true;
					this.selectedFeature[index].negotiationCode_access[0]["edit"] = true;
					this.selectedFeature[index].negotiationCode_access[0]["view"] = true;
					this.selectedFeature[index].negotiationCode_access[0]["delete"] = true;
					this.selectedFeature[index].negotiationCode_access[0]["fullAccess"] = true;
				} else {
					this.selectedFeature[index].negotiationCode_access[0][accessType] = true;
				}
			} else if (accessType === "full-access") {
				// Add the feature to selectedFeature array with the specific access type
				let dataFindNegotiationCodeAccess = null;
				const findFeatureAccess = this.features.find((data) => data.name == feature);
				if (findFeatureAccess) {
					const findNegotiationCodeAccess = findFeatureAccess.negotiationCode_access.find((data) => data.negotiationCode == controls.negotiationCode.value);
					if (findNegotiationCodeAccess) dataFindNegotiationCodeAccess = findNegotiationCodeAccess;
				}
				const newFeature = {
					name: feature,
					negotiationCode_access: [
						{
							negotiationCode: controls.negotiationCode.value,
							edit: true,
							view: true,
							add: true,
							delete: true,
							fullAccess: true,
						},
					],
				};
				newFeature.negotiationCode_access[0]["fullAccess"] = true;
				this.selectedFeature.push(newFeature);
			} else {
				// Add the feature to selectedFeature array with the specific access type
				let dataFindNegotiationCodeAccess = null;
				const findFeatureAccess = this.features.find((data) => data.name == feature);
				if (findFeatureAccess) {
					const findNegotiationCodeAccess = findFeatureAccess.negotiationCode_access.find((data) => data.negotiationCode == controls.negotiationCode.value);
					if (findNegotiationCodeAccess) dataFindNegotiationCodeAccess = findNegotiationCodeAccess;
				}
				const newFeature = {
					name: feature,
					negotiationCode_access: [
						{
							negotiationCode: controls.negotiationCode.value,
							edit: dataFindNegotiationCodeAccess ? dataFindNegotiationCodeAccess.edit : false,
							view: dataFindNegotiationCodeAccess ? dataFindNegotiationCodeAccess.view : false,
							add: dataFindNegotiationCodeAccess ? dataFindNegotiationCodeAccess.add : false,
							delete: dataFindNegotiationCodeAccess ? dataFindNegotiationCodeAccess.delete : false,
							fullAccess: dataFindNegotiationCodeAccess ? dataFindNegotiationCodeAccess.fullAccess : false,
						},
					],
				};
				newFeature.negotiationCode_access[0][accessType] = true;
				this.selectedFeature.push(newFeature);
			}
		} else {
			// If unchecked, update the specific access type to false or remove the feature from selectedFeature array
			if (index !== -1) {
				// Update the specific access type
				if (accessType === "full-access") {
					this.selectedFeature[index].negotiationCode_access[0]["add"] = false;
					this.selectedFeature[index].negotiationCode_access[0]["edit"] = false;
					this.selectedFeature[index].negotiationCode_access[0]["view"] = false;
					this.selectedFeature[index].negotiationCode_access[0]["delete"] = false;
					this.selectedFeature[index].negotiationCode_access[0]["fullAccess"] = false;
				} else {
					this.selectedFeature[index].negotiationCode_access[0][accessType] = false;
				}
			} else if (accessType === "full-access") {
				// Add the feature to selectedFeature array with the specific access type
				let dataFindNegotiationCodeAccess = null;
				const findFeatureAccess = this.features.find((data) => data.name == feature);
				if (findFeatureAccess) {
					const findNegotiationCodeAccess = findFeatureAccess.negotiationCode_access.find((data) => data.negotiationCode == controls.negotiationCode.value);
					if (findNegotiationCodeAccess) dataFindNegotiationCodeAccess = findNegotiationCodeAccess;
				}
				const newFeature = {
					name: feature,
					negotiationCode_access: [
						{
							negotiationCode: controls.negotiationCode.value,
							edit: false,
							view: false,
							add: false,
							delete: false,
							fullAccess: false,
						},
					],
				};
				newFeature.negotiationCode_access[0]["fullAccess"] = false;
				this.selectedFeature.push(newFeature);
			} else {
				// Add the feature to selectedFeature array with the specific access type
				let dataFindNegotiationCodeAccess = null;
				const findFeatureAccess = this.features.find((data) => data.name == feature);
				if (findFeatureAccess) {
					const findNegotiationCodeAccess = findFeatureAccess.negotiationCode_access.find((data) => data.negotiationCode == controls.negotiationCode.value);
					if (findNegotiationCodeAccess) dataFindNegotiationCodeAccess = findNegotiationCodeAccess;
				}
				const newFeature = {
					name: feature,
					negotiationCode_access: [
						{
							negotiationCode: controls.negotiationCode.value,
							edit: dataFindNegotiationCodeAccess ? dataFindNegotiationCodeAccess.edit : false,
							view: dataFindNegotiationCodeAccess ? dataFindNegotiationCodeAccess.view : false,
							add: dataFindNegotiationCodeAccess ? dataFindNegotiationCodeAccess.add : false,
							delete: dataFindNegotiationCodeAccess ? dataFindNegotiationCodeAccess.delete : false,
							fullAccess: dataFindNegotiationCodeAccess ? dataFindNegotiationCodeAccess.fullAccess : false,
						},
					],
				};
				newFeature.negotiationCode_access[0][accessType] = false;
				this.selectedFeature.push(newFeature);
			}
		}
	}

	/**
	 * changeAmount
	 * @param event
	 * @param id
	 */
	changeAmount(event: any) {
		this.toCurrency(event);
	}

	/**
	 * toCurrency
	 * @param event
	 * @param id
	 * @returns
	 */
	toCurrency(event: any) {
		const controls = this.negotiationCodeForm.controls;

		let value = event.target.value;

		let number_string = value.replace(/[^-\d,]/g, "").toString(),
			isNegative = false;

		if (number_string.startsWith("-")) {
			isNegative = true;
			number_string = number_string.substr(1);
		}

		let split = number_string.split(","),
			sisa = split[0].length % 3,
			rupiah = split[0].substr(0, sisa),
			ribuan = split[0].substr(sisa).match(/\d{3}/gi);

		// tambahkan titik jika yang di input sudah menjadi value ribuan
		let separator;
		if (ribuan) {
			separator = sisa ? "." : "";
			rupiah += separator + ribuan.join(".");
		}

		if (split.length > 1) {
			rupiah += "," + split[1];
		}

		if (isNegative) {
			rupiah = "-" + rupiah;
		}

		controls.price.setValue(rupiah);
		controls.price_float.setValue(rupiah);

		return rupiah;
	}

	// Helper function to add a negotiationCode to the hierarchy
	private addNegotiationCodeToHierarchy(item: any, negotiationCode: string): void {
		if (item.negotiationCode.indexOf(negotiationCode) === -1) {
			item.negotiationCode.push(negotiationCode);
		}
	}

	// Helper function to remove a negotiationCode from the hierarchy
	private removeNegotiationCodeFromHierarchy(item: any, negotiationCode: string): void {
		const negotiationCodeIndex = item.negotiationCode.indexOf(negotiationCode);
		if (negotiationCodeIndex !== -1) {
			item.negotiationCode.splice(negotiationCodeIndex, 1);
		}
	}
}
